body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



:root {
  --off-white: #fafafa;
}
.nav {
  background-color: black;
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  width: 100%;
  height: 10vmin;
  z-index: 1000;
  box-shadow: 0 4px 14px 0 rgba(0, 0, 0, 0.15);
}


.card {
  width: 20em;
  text-align:center;
}

.title {
  font-size: 6vmin;
  text-align: center;
}

.nav-tabs {
  background-color: transparent;
  height: 4vmin;
  align-items: center;

}

.section-heading {
  margin-top: 5%;
  font-size: 4vmin;
}

.nav-item-projects {
  display: inline;
  margin-left: 1%;
  color: rgb(177, 177, 177);
  vertical-align: middle;
  font-size: 1.5vmin;
  font-weight: 200;
}

.nav-logo {
  height: 6vmin;
  width: 6vmin;
  border-radius: 50%
  /* animation: App-logo-spin infinite 20s linear; */
}

.nav-logo:hover {
  cursor: pointer;
}

.nav .nav-content {
  max-width: 900px;
  padding: 0rem 1%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}
.nav-items {
  margin: 0;
  padding: 0;
}
.nav-item {
  display: inline;
  margin-left: 2rem;
  color: rgb(177, 177, 177);
  vertical-align: middle;
  font-size: 3.5vmin;
  font-weight: 300;
}

.nav-item:hover {
  color: rgb(255, 255, 255);
  cursor: pointer;
}

.section { height: 90vh;
  background: radial-gradient(ellipse at bottom, #d3d5e2 0%, #121420 100%);
  color: white;
  z-index: 3;
  position: relative;
  /* background-size: 100% 100%; */
}

.section-dark {
  background-image: url(https://i.pinimg.com/originals/d2/c6/41/d2c6410e5041dee3d36748ac1c32f6ea.jpg);
  z-index: 0;
  color: white;
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
}

.section-content {
  max-width: 100%;
  margin: 0 auto;
  padding-top: 40px;
  font-size: 3vmin;
}



.row-main{
  margin-top: 15vmin;
}

.col-lg-10 {
  color: rgb(173, 173, 173);
  margin-left: 0;
  text-align: center;
  max-width: 55%;
}

.text-uppercase {
  font-size:6vmin;
  font-weight: 200;
}

.col-lg-8 {
  margin-top: 8vmin;
  text-align: center;
  max-width: 55%;
} 

.mt-5 {
  margin-top: 1vmin !important;
}

.p-2 {
  padding: 0.5 vmin !important;
}

.col-lg-4 {
  margin-top:2rem;
}

.email {
  unicode-bidi:bidi-override; 
  direction: rtl;
}

.text-faded {
  font-weight: 200;
}

.iframe-setup {
  margin-top: 2%;
  margin-left: 20%;
  height: 60%;
  width: 60%;
  vertical-align: middle;
  text-align: center;
}

.iframe-container {
  overflow: hidden;
  padding-top: 56.25%;
  position: relative;
  text-align: center;
}

.iframe-container iframe {
   border: 0;
   height: 100%;
   left: 0;
   position: absolute;
   top: 0;
   width: 100%;
}

/* 4x3 Aspect Ratio */
.iframe-container-4x3 {
  padding-top: 75%;
}

.row-skills {
  margin-top: 0;
  text-align:center;
}

.pic80{
  width: 8vmin; 
  height: 8vmin; 
  border-radius: 10%
}

.react-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
}

.pic100{
  width: 10vmin; 
  height: 8vmin; 
  border-radius: 10%
}

.carousel-inner > .active > img {
  margin-top: 3%;
  margin-left:30%;
  width: 40%;
  height: 60vmin;
  border-radius: 10%;
  }
  
.carousel-inner > .item > img {
  margin-top: 3%;
  margin-left:30%;
  width: 40%;
  height: 60vmin;
  border-radius: 10%;
  }

.resume {
  margin-top: 3%;
  margin-left:30%;
    width: 40%;
    height: 60vmin;
    border-radius: 10%;
}

.carousel-inner > .active > .resume-button {
  margin-left: 45%;
  width: 10%;
}

.resume-button {

  height: 4.5vmin;
  margin-top: 1vmin;
  margin-left: 45%;
  width: 10%;
  justify-content: space-between;
  align-items: center;
}

.carousel-inner > .active > .connect {
  margin-left:25%;
  width: 50%;
  height: 40vmin;
  border-radius: 10%;
  }

.project-pic {
  margin-top: 10px;
  margin-left:30%;
  width: 40%;
  height: 40vmin;
  border-radius: 3%;

}

.project-description {
  margin-top: 1%;
  width: 50%;
  font-size: 2vmin;
  margin-left: 25%;
}

.project-button {
  height: 4.5vmin;
  margin-top: 2vmin;
  margin-left: 45%;
  width: 10%;
  justify-content: space-between;
  align-items: center;
}

.text-uppercase {
  text-align: center!important;
}

.mb-3 {
  font-size: 3vmin !important;
}

.my-life-summary {
  width: 70%;
  margin-left: 15%;
}

.service-box {
  margin-top: 1vh;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }

}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }

}

